.App {
  height: 100%;
  text-align: center;
}

.header {
  background-color: #222;
  height: 10px;
  color: white;
}

.main {
  height: calc(100% - 10px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0px;
}

.more {
  color: #222;
  position: absolute;
  bottom: 0px;
  -webkit-animation: arrow 0.5s ease-in-out infinite alternate;
          animation: arrow 0.5s ease-in-out infinite alternate;
  cursor: default;
}

.more div {
  margin-bottom: 5px;
}

.more b {
  font-size: 15px;
}

@-webkit-keyframes arrow {
    from {margin-bottom: 5px;}
    to {margin-bottom: 10px;}
}

@keyframes arrow {
    from {margin-bottom: 5px;}
    to {margin-bottom: 10px;}
}

.about {
  margin-bottom: 30px;
  max-width: 600px;
  display: inline-block;
  padding: 0px 50px;
}

.about p {
  color: gray;
}

.about pre {
  text-align: left;
  font-family: monospace;
  display: inline-block;
  border: 1px solid #dedbdb;
  border-radius: 10px;
  padding: 15px;
  background-color: #fbfafa;
}


.about pre {
  font-size: 14px;
}

@media (max-width: 800px) {
  .about pre {
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  .about pre {
    font-size: 10px;
  }
}

@media (max-width: 450px) {
  .about {
    display: none;
  }
  .more {
    display: none;
  }
  .twitter {
    display: none;
  }
}


.about .section {
  margin-bottom: 50px;
}
.try-miner .hashrate {
  font-weight: 100;
  border-radius: 10px;
  border: 1px solid #222;
  padding: 9px 13px 7px 13px;
  border-right: none;
  border-radius: 10px 0px 0px 10px;
  margin-right: -5px;
  margin-top: 0px;
}

.miner-button {
  color: white;
  width: 60px;
  display: inline-block;
  background-color: #1d1818;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 13px;
  padding-top: 11px;
  padding-bottom: 8px;
  padding-right: 13px;
  padding-left: 12px;
  border: 1px solid #222;
}

.about .options {
  text-align: left;
}

.about .options b {
  font-family: monospace;
  color: black;
  border: 1px solid #eceaea;
  padding: 5px;
  background-color: #fbfafa;
  font-weight: 100;
  border-radius: 5px;
}

.about .api-title {
  font-weight: bold;
  color: black;
  margin-top: 40px;
}

.twitter {
  font-size: small;
  color: gray;
  margin-top: 200px;
  margin-bottom: 30px;
}

.twitter a {
  color: black;
}

.section a {
  color: black;
}


