.worker {
  height: 550px;
  width: calc(100% - 200px);
  max-width: 600px;
  min-width: 300px;
}

.worker .currency {
  position: relative;
  font-size: 30px;
}

.worker .address {
  font-weight: bold;
  margin-bottom: 35px;
}

.worker .address span {
  font-weight: 100;
  color: gray;
}

.error {
  text-align: center;
}

.error p {
  font-size: 20px;
}

.error .info {
  color: gray;
  font-size: 15px;
}

.worker .third {
  width: 33%;
  display: inline-block;
}

.worker .third div {
  font-weight: bold;
}

.worker .third p {
  font-size: 25px;
}

.exit {
  color: white;
  border: 1px solid #222;
  width: 40px;
  display: inline-block;
  background-color: #1d1818;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 30px;
  margin-bottom: 30px;
  font-weight: 300;
  font-size: 13px;
  padding-top: 10px;
  padding-bottom: 9px;
  padding-right: 13px;
  padding-left: 12px;
}

.worker .pool-info {
  color: gray;
  font-size: small;
}

.worker .pool-info p {
  display: inline-block;
  font-weight: bold;
}

.worker .pool-info .row {
  display: block;
}

.worker .pool-info .column {
  display: inline-block;
  width: 50%;
}

.worker .pool-info .left {
  text-align: left;
}

.worker .pool-info .right {
  text-align: right;
}

.worker .pool-info .important {
  margin: 20px;
  font-size: medium;
  color: black;
}


