.hero {
  height: 350px;
  width: calc(100% - 200px);
  max-width: 400px;
  min-width: 200px;
}

.hero-currency {
  position: relative;
  font-size: 30px;
}

.hero img {
  cursor: pointer;
}

.hero select {
  margin: 31px;
  padding: 5px;
}

.hero a { 
  text-decoration: none;
}

.hero .access {
  display: inline;
  color: white;
  border: 1px solid #222;
  border-left: none;
  background-color: #1d1818;
  border-top: none;
  border-radius: 0px 10px 10px 0px;
  font-family: Helvetica;
  font-weight: 300;
  font-size: 13px;
  padding-top: 10px;
  padding-bottom: 9px;
  padding-right: 13px;
  padding-left: 12px;
  outline: none;
}

.hero input {
  outline: none;
  border-radius: 10px 0px 0px 10px;
  border: 1px solid lightgray;
  padding: 10px;
  width: calc(100% - 100px);
}

.hero-currency a {
  color: gray;
  font-weight: 100;
  font-size: 14px;
  margin-left: 10px;
  position: absolute;
  bottom: 5px;
  cursor: pointer;
  border-bottom: 1px solid transparent;
}

.hero-currency a:hover {
  border-bottom: 1px dashed gray;
}

.hero .info {
  color: #807a7a;
  font-size: small;
}


